.program {
  background-image: url("../img/sketch-bg-desktop@1x.png");
  background-repeat: no-repeat;
  background-size: 60%;
  background-position: top right;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/sketch-bg-desktop@2x.png");
  }
}

.program__container {
  padding: 104px 304px 23px 210px;
  flex-direction: row;
  flex-wrap: nowrap;

  @media (min-width: 1024px) and (max-width: 1300px) {
    padding: 104px 100px 23px;
  }

  @media (min-width: $min-tablet-width) and (max-width: $max-tablet-width) {
    padding: 104px 20px 23px 100px;
  }

  @media (max-width: $max-mobile-width) {
    padding: 0;
    width: 100%;
  }

  h2 {
    @include reset;
    font-family: "montserrat", "Arial", sans-serif;
    font-size: 40px;
    line-height: 49px;
    font-weight: 700;
    color: $blue;
    letter-spacing: -1px;
    text-transform: uppercase;
    margin-bottom: 70px;

    @media (min-width: 1024px) and (max-width: 1300px) {
      text-align: center;
    }

    @media (max-width: $max-mobile-width) {
      font-size: 32px;
      line-height: 39px;
      text-align: center;
      margin-bottom: 14px;
    }
  }

  h3 {
    @include reset;
    font-size: 28px;
    font-family: "montserrat", "Arial", sans-serif;
    line-height: 34px;
    font-weight: 700;
    color: $blue;
    letter-spacing: -1px;
    margin-bottom: 24px;

    @media (max-width: $max-mobile-width) {
      font-size: 24px;
      line-height: 29px;
      letter-spacing: -0.8px;
      margin-bottom: 18px;
    }
  }

  p {
    @include reset;
    font-size: 15px;
    line-height: 25px;
    margin-bottom: 20px;
    opacity: 0.87;

    @media (max-width: $max-mobile-width) {
      font-size: 14px;
      line-height: 22px;
      margin-bottom: 15px;
    }
  }
}

.program__content {
  display: flex;

  @media (max-width: $max-mobile-width) {
    flex-direction: column;
  }
}

.program__item {
  display: none;
  width: 560px;
  padding-top: 15px;

  &--active {
    display: block;
  }

  @media (max-width: $max-mobile-width) {
    width: 100%;
    padding: 12px 16px 0;
  }
}

.program__choice {
  display: flex;
  flex-direction: column;
  margin-right: 66px;

  @media (max-width: $max-mobile-width) {
    flex-direction: row;
    transform: unset;
    margin-bottom: 20px;
  }
}

.program__choice-item {
  border: 0;
  outline: 0;
  cursor: pointer;
  position: relative;
  margin-right: 30px;
  font-family: "firasans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 18px;
  padding: 26px 0 25px 74px;
  background-color: transparent;
  text-align: left;
  width: 130px;
  opacity: 0.87;

  &::before {
    content: "";
    position: absolute;
    left: 20px;
    top: 50%;
    transform: translateY(-50%);
    background-image: url("../img/sprite.png");
    background-repeat: no-repeat;
  }

  &:hover:not(.program__choice-item--active) {
    color: $yellow;
  }

  @media (max-width: $max-mobile-width) {
    display: flex;
    justify-content: flex-end;
    font-size: 15px;
    line-height: 18px;
    opacity: 0.5;
    padding: 15px 20px 15px 20px;
    margin: 0;
  }

  &--general {
    @media (max-width: $max-mobile-width) {
      width: unset;
      padding-left: 20px;
    }
  }

  &--internship {
    @media (max-width: $max-mobile-width) {
      width: unset;
      padding-left: 70px;
    }
  }

  &--religious {
    @media (max-width: $max-mobile-width) {
      width: unset;
      padding-left: 50px;
    }
  }

  &--active {
    background: linear-gradient(to right, $yellow 0%, $yellow-2 100%);
    text-transform: uppercase;
    font-size: 14px;
    line-height: 19px;
    letter-spacing: 0.5px;
    color: $white;

    &::before {
      background-position: 0 -1px;
    }

    &::after {
      content: "";
      display: block;
      height: 0;
      width: 0;
      position: absolute;
      top: 0;
      right: -25px;
      border-left: 26px solid $yellow-2;
      border-top: 34px solid transparent;
      border-bottom: 34px solid transparent;

      @media (max-width: $max-mobile-width) {
        display: none;
      }
    }

    @media (max-width: $max-mobile-width) {
      border-radius: 100px;
    }

    &:hover {
      color: $white;
    }
  }
}

.program__choice-item--general::before {
  width: 28px;
  height: 33px;
  background-position: 0 -45px;
}

.program__choice-item--general.program__choice-item--active::before {
  background-position: 0 -1px;
}

.program__choice-item--general:hover:not(.program__choice-item--active)::before {
  background-position: 0 -88px;
}

.program__choice-item--academic::before {
  width: 36px;
  height: 35px;
  background-position: -44px -43px;
}

.program__choice-item--academic.program__choice-item--active::before {
  background-position: -44px 0;
}

.program__choice-item--academic:hover:not(.program__choice-item--active)::before {
  background-position: -44px -86px;
}

.program__choice-item--internship::before {
  width: 32px;
  height: 25px;
  background-position: -96px -53px;
}

.program__choice-item--internship.program__choice-item--active::before {
  background-position: -96px -9px;
}

.program__choice-item--internship:hover:not(.program__choice-item--active)::before {
  background-position: -96px -96px;
}

.program__choice-item--volunteering::before {
  width: 32px;
  height: 29px;
  background-position: -144px -49px;
}

.program__choice-item--volunteering.program__choice-item--active::before {
  background-position: -144px -7px;
}

.program__choice-item--volunteering:hover:not(.program__choice-item--active)::before {
  background-position: -144px -92px;
}

.program__choice-item--religious::before {
  width: 23px;
  height: 25px;
  background-position: -192px -53px;
}

.program__choice-item--religious.program__choice-item--active::before {
  background-position: -192px -10px;
}

.program__choice-item--religious:hover:not(.program__choice-item--active)::before {
  background-position: -192px -95px;
}

.swiper-container {
  height: 100%;
  width: 296px;

  @media (max-width: $max-mobile-width) {
    width: 100%;
  }
}

.swiper-slide {
  @media (max-width: $max-mobile-width) {
    width: 150px;
  }
}

.swiper-slide-next,
.program__choice-item--active {
  @media (max-width: $max-mobile-width) {
    width: 150px;
  }
}
